* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  height: 100vh;
  background: linear-gradient(#38a5df, #000) fixed;
  margin-top: 32px;
  margin-left: 32px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

ul {
  width: 256px;
  margin-top: 0;
  padding-left: 0;
  list-style: none;
}

.list {
  min-height: 32px;
  transition-property: all;
  transition-duration: .5s;
  display: flex;
}

.list:hover {
  min-height: 64px;
  background-color: rgba(117, 117, 117, .48);
  border-radius: 8px;
  padding-left: 16px;
  font-weight: bold;
}

#search-box {
  font: inherit;
  max-width: 320px;
  border: 2px solid #000;
  border-radius: 50px;
  margin-bottom: 16px;
  padding: 8px;
}

#search-box:focus {
  border: 4px solid #000;
  outline: none;
}

.title {
  font-weight: bold;
}

.img_wrapper {
  pointer-events: none;
  width: 32px;
  height: auto;
  justify-content: center;
  display: inline-flex;
}

img {
  width: 100%;
  height: auto;
  align-self: center;
}

span {
  pointer-events: none;
  align-self: center;
  padding-left: 4px;
}

h3 {
  margin: 0;
  font-size: 24px;
  display: inline;
}

.country-info {
  position: fixed;
  top: 32px;
  left: 320px;
}

@media screen and (max-width: 580px) {
  body {
    padding-right: 20px;
  }

  .country-info {
    max-width: 320px;
    margin-bottom: 10px;
    position: static;
    box-shadow: 0 0 1rem rgba(0, 0, 0, .2);
  }

  .country-info:before {
    content: "";
    background: inherit;
    z-index: -1;
    filter: blur(10px);
    margin: -20px;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    box-shadow: inset 0 0 2000px rgba(255, 255, 255, .5);
  }
}

